@import url(https://fonts.googleapis.com/css2?family=Arimo&family=Rosario&display=swap);
:root {
  --title-font: 'Rosario', sans-serif;
  --content-font: 'Arial', 'Arimo', sans-serif;

  --money-green-light: #edf3f0;
  --money-green: #cadcd3;
  --money-green-dark: #293e33;

  font-size: 16px;
  --h1: 3.25rem;
  --h2: 2rem;
  --body: 1.25rem;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-size: 1.25rem;
  font-size: var(--body);
  font-family: 'Arial', 'Arimo', sans-serif;
  font-family: var(--content-font);

  background-color: #cadcd3;

  background-color: var(--money-green);
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 2rem;
}

header {
  margin-left: 10rem;
  margin-bottom: 2rem;
}

.title {
  font-family: var(--title-font);
  font-size: var(--h1);
  margin: 0;
}

.subtitle {
  font-family: var(--title-font);
  font-size: var(--h2);
  margin: 0;
}

.hero {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.hero-img-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.hero-img {
  width: 720px;
  border-radius: 4rem;
}

.hero-content {
  margin: 2rem;
}

.hero-buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.ppp-button {
  text-decoration: none;
  text-align: center;
  outline: none;
  font-family: var(--content-font);
  font-size: var(--body);
  border: 2px solid var(--money-green-dark);
  border-radius: 0.5rem;
  padding: 1rem;
  min-width: 120px;
  transition: all 0.1s linear;
}

.ppp-button:hover,
.ppp-button:focus {
  outline: none;
  box-shadow: 0 0 1px 1px var(--money-green-dark);
}

.ppp-button:active {
  outline: none;
  box-shadow: 0 0 3px 3px var(--money-green-dark);
}

.secondary-button {
  color: var(--money-green-dark);
  background-color: #fff;
}

.primary-button {
  color: #fff;
  background-color: var(--money-green-dark);
}

.read-more {
  margin: 6rem 12rem;
}

.read-more-title {
  text-align: center;
}

.read-more-copy {
  text-align: justify;
}

header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.about-us-container {
  margin: 6rem 12rem;
}

.about-link {
  margin-left: 2rem;
  margin-right: 2rem;
}

.team-members {
  margin-top: 6rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.member {
  display: grid;
  grid-template-columns: 1fr 200px 1fr;
  margin: 1rem;
}

.profile-pic {
  width: 120px;
  border-radius: 50%;
  grid-column: 1 / span 1;
}

.demo-info {
  grid-column: 2 / span 1;
  margin-top: 2rem;
}

.demo-info * {
  margin: 0;
}

.social-icons {
  grid-column: 3 / span 1;
  margin: 2rem;
}

.icon {
  width: 48px;
}

.loan-form-container {
  background-color: var(--money-green-light);
  border: 1px solid var(--money-green-dark);
  border-radius: 0.5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  overflow: hidden;
  width: 800px;
  margin: 0 auto;
}

.loan-form-title {
  margin: 2rem;
}

.loan-form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 600px;
}

.form-link {
  color: #000;
}

.naics,
.jobsreported,
.lender {
  display: grid;
  grid-template-columns: 224px 1fr;
  margin: 1.5rem 1.5rem 3rem 3rem;
}

.lender-type {
  grid-column: 1 / span 2;
}

.naics-label,
.jobsreported-label,
.lender-label {
  grid-column: 1 / span 1;
  padding: 0.5rem;
}

.form-input {
  font-size: var(--body);
  border: 1px solid var(--money-green-dark);
  border-radius: 0.25rem;
  padding: 0.5rem;
  max-width: 300px;
}

#naics,
#jobsreported,
#lender {
  grid-column: 2 / span 1;
}

.naics-help {
  grid-column: 2 / span 1;
}

.form-buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin: 2rem 0 4rem;
}

.results {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 600px;
  margin: 0 auto;
}

